import React from 'react';
import { Modal } from 'antd';

import {
  defaultImgUrl,
  defaultProbelUrl,
} from '~/utils/constant';

export const showConfigModel = (config: string) => {
    const { alias, ratio, isAsync, probelUrl, imgUrl } = JSON.parse(config);

    let waring = '';
    if (!/^__\w{1,10}$/.test(alias)) {
        waring = '';
    } else if (!/^(https|http|ftp)/.test(probelUrl)) {
        waring = 'url';
    } else if (!/^(https|http|ftp)/.test(imgUrl)) {
      waring = 'url';
  }

    const content = (
        <div>
            {alias !== '__fw' && <div>: {alias}</div>}
            {Number(ratio) !== 1 && <div>: {ratio}</div>}
            {Number(isAsync) !== 0 && <div></div>}
            {probelUrl !== defaultProbelUrl && <div>: {probelUrl}</div>}
            {imgUrl !== defaultImgUrl && <div>: {probelUrl}</div>}
            {waring && <div style={{ color: 'red' }}>{waring}</div>}
        </div>
    );

    return new Promise((reslove) => {
        if (waring) {
            Modal.warning({
                title: '',
                okText: '',
                okType: 'danger',
                content,
            });
        } else {
            Modal.confirm({
                title: '',
                content,
                okText: '',
                cancelText: '',
                onOk: () => {
                    reslove();
                },
            });
        }
    });
};
