import React from 'react';

import { Wrapper } from './styled';
import {
  Button,
  Tooltip,
} from 'antd';

class TimeChoose extends React.Component {

  render() {
    return (
      <Wrapper>
        <Tooltip title={""}>
         <Button disabled></Button>
        </Tooltip>
        <span className="space"></span>
        <Tooltip title={""}>
         <Button disabled></Button>
        </Tooltip>
        <span className="space"></span>
        <Button>12</Button>
      </Wrapper>
    );
  }
}

export default TimeChoose;