import axios from 'axios';
import { message } from 'antd';

interface IFRequestParam {
    url: string;
    msg?: string;
    config?: any;
    data?: any;
}
/**
 * get
 * @param url       
 * @param msg       
 * @param headers   header
 */
export const get = ({ url, msg = '', config }: IFRequestParam) =>
    axios
        .get(url, config)
        .then(res => res.data)
        .catch(err => {
            console.log(err);
            message.warn(msg);
        });

/**
 * post
 * @param url       
 * @param data      
 * @param msg       
 * @param headers   header
 */
export const post = ({ url, data, msg = '', config }: IFRequestParam) =>
    axios
        .post(url, data, config)
        .then(res => res.data)
        .catch(err => {
            console.log(err);
            message.warn(msg);
        });
