import React from 'react';
import {
  Form,
  Input,
  Modal,
  message,
} from 'antd';
import { FormComponentProps } from 'antd/lib/form/Form';

import { editApp } from '~/ajax';

const formItemLayout = {
  labelCol: {
    xs: { span: 20 },
    sm: { span: 6 },
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 16 },
  },
};

interface IProps extends FormComponentProps {
  visible: boolean,
  app: any,
  hideEditMOdal: () => void,
};
class EditModal extends React.Component<IProps> {
  state = {
    confirmLoading: false,
  };

  private handleOk = () => {
    this.props.form!.validateFields((err, values) => {
      if (err) {
        return ;
      }
      const { app } = this.props;
      this.setState({ confirmLoading: true });
      editApp(app.id, values.appInfo).then((res: any) => {
        if (res.resType === 0) {
          message.success('', 0.5, () => {
            window.location.reload();
          });
          return ;
        }
        message.error('');
      }).finally(() => {
        this.setState({ confirmLoading: false });
      });
    });
  };


  render () {

    const {
      confirmLoading,
    } = this.state;
    console.log(this.props.app, this.props.app|| {})
    let {
      app,
      visible: modalVisible,
      hideEditMOdal= ()=> {}
    } = this.props;
    app = app || {}
    const { getFieldDecorator } = this.props.form!;

    return (
      <Modal
      title=""
      centered
      okText=""
      cancelText=""
      visible={modalVisible}
      onOk={this.handleOk}
      confirmLoading={confirmLoading}
      onCancel={hideEditMOdal}
    >
      <Form {...formItemLayout}>
        <Form.Item label="">
          {getFieldDecorator('appName', {
            initialValue: app.appName || '',
          })(<Input disabled />)}
        </Form.Item>
        <Form.Item label="">
          {getFieldDecorator('appInfo', {
            initialValue: app.appInfo || '',
          })(<Input.TextArea placeholder="" />)}
        </Form.Item>
      </Form>
    </Modal>
    );
  }
}

export default Form.create<IProps>()(EditModal);
