import React from 'react';
import { Button, Radio } from 'antd';
import { connectAlita } from 'redux-alita';
import { STATE_CURRENT_APP } from '~/redux/reduxStateName';

import RadioWithCustom from '../RadioWithCustom';
import { showConfigModel } from './tool';
import { getJSONByForm } from '~/utils';
import {
  defaultImgUrl,
  defaultProbelUrl,
} from '~/utils/constant';

import { SpecialConfigWrapper } from './styled';

function ConfigItem(props: any) {
  const { title, children } = props;
  return (
    <div>
      <h1>{title}</h1>
      <div className="config_content">{children}</div>
    </div>
  );
}

class SpecialSetter extends React.PureComponent<any> {
  formRef: React.RefObject<any>;

  constructor(props: any) {
    super(props);
    this.formRef = React.createRef();
  }

  private hadleSumbit = () => {
    const formElem = this.formRef.current;
    const preSpecialConfig = getJSONByForm(formElem);
    showConfigModel(preSpecialConfig).then(() => {
      this.props.setspecialConfig(preSpecialConfig);
    });
  };

  render() {
    const { specialConfig } = this.props;
    const currentApp = this.props[STATE_CURRENT_APP] && this.props[STATE_CURRENT_APP].data;

    const config = specialConfig ? JSON.parse(specialConfig) : {};

    return (
      <SpecialConfigWrapper>
        <form ref={this.formRef}>
          <div className="top_tip">
            <p>,</p>
            <p>
              ,,,
            </p>
            <p className="title_tip">
              ps: ,
              , 
            </p>
          </div>
          <div className="config">
            <ConfigItem title="ID">
              <p className="wid">{currentApp.id}</p>
              <p>
                ID(wid),
                .ID
              </p>
            </ConfigItem>

            <ConfigItem title="">
              <RadioWithCustom
                name="alias"
                value={config.alias}
                defaultValue="__fw"
              />
              <p>
                window.__fw,,
                <br />
                1.__;210;3.,;
              </p>
            </ConfigItem>
            <ConfigItem title="">
              <Radio.Group name="ratio" defaultValue="1">
                <Radio value="1">100%()</Radio>
                <Radio value="0.5">50%</Radio>
                <Radio value="0.25">25%</Radio>
                <Radio value="0.1">10%</Radio>
              </Radio.Group>
              <p>
                , cpu,
                
              </p>
            </ConfigItem>
            <ConfigItem title="">
              <Radio.Group name="isAsync" defaultValue="0">
                <Radio value="0">()</Radio>
                <Radio value="1"></Radio>
              </Radio.Group>
              <p>
                 body
                ,,,
              </p>
              <p className="tip">
                : ,
                error.
                <br />
                <span className="space"></span>
                50ms,,.
              </p>
            </ConfigItem>
            <ConfigItem title="">
              <p>
                ,,
              </p>
              <ul>
                <li>
                  
                  <a href="https://github.com/fuWenMr/graduationProject">
                    git
                  </a>
                  clone, 
                </li>
                <li>
                  (
                  <a href={defaultProbelUrl} download="probel.js">
                    
                  </a>
                  ),url,
                </li>
                <li> </li>
              </ul>
              <RadioWithCustom
                title="url"
                name="probelUrl"
                value={config.probelUrl}
                defaultValue={defaultProbelUrl}
              />
              <RadioWithCustom
                title=""
                name="imgUrl"
                value={config.imgUrl}
                defaultValue={defaultImgUrl}
              />
              <p className="tip">
                : https, urlhttps
              </p>
            </ConfigItem>
            <Button type="primary" onClick={this.hadleSumbit}>
              {' '}
              {' '}
            </Button>
          </div>
        </form>
      </SpecialConfigWrapper>
    );
  }
}

export default connectAlita([STATE_CURRENT_APP])(SpecialSetter);
