import React from 'react';
import { Form, Icon, Input, Button, Modal } from 'antd';
import { FormComponentProps } from 'antd/lib/form/Form';



import { doRegister } from '~/ajax';

const FormItem = Form.Item;

interface IProps extends FormComponentProps {
  changeType: any,
};

class LoginPanel extends React.PureComponent<IProps> {
  state = {
    errorMsg: '',
    loading: false,
  };

  private onRegister = () => {
    const { changeType } = this.props;
    Modal.success({
      title: '',
      content: '',
      okText: '',
      onOk: changeType.toLogin, 
    });
  }

  handleSubmit = (e: React.FormEvent) => {
    
    e.preventDefault();
    this.props.form!.validateFields((err, values) => {
      if (err) {
        return ;
      }
      
      if (values.password !== values.passwordCheck) {
        this.setState({
          errorMsg: '',
        });
      } else {
        this.setState({
          loading: true,
        });

        doRegister(values.userName, values.password, values.ali).then((data: any) => {
          console.log('', data);
          if (data.resType === 0) {
            // 
            this.onRegister();
          } else {
            console.log('');
            this.setState({ errorMsg: data.msg || ''});
          }
          }).finally(() => {
            this.setState({ loading: false });
        });
      }
    });
  };

  render() {
    const { 
      changeType,
    } = this.props;
    const {
      errorMsg,
      loading,
    } = this.state;

    const { getFieldDecorator } = this.props.form!;
    return (
      <Form onSubmit={this.handleSubmit} style={{ maxWidth: '300px' }}>
        <FormItem>
          {getFieldDecorator('userName', {
            rules: [
              { required: true, message: 'email' },
              { type: 'email', message: 'email' }
            ],
          })(
            <Input
              prefix={<Icon type="user" style={{ fontSize: 13 }} />}
              placeholder="email"
            />
          )}
        </FormItem>
        <FormItem>
          {getFieldDecorator('ali', {
            rules: [{ required: true, message: '!' }],
          })(
            <Input
              prefix={<Icon type="meh" style={{ fontSize: 13 }} />}
              placeholder=""
            />
          )}
        </FormItem>
        <FormItem>
          {getFieldDecorator('password', {
            rules: [{ required: true, message: '!' }],
          })(
            <Input
              prefix={<Icon type="lock" style={{ fontSize: 13 }} />}
              type="password"
              placeholder=""
            />
          )}
        </FormItem>
        <FormItem>
          {getFieldDecorator('passwordCheck', {
            rules: [{ required: true, message: '!' }],
          })(
            <Input
              prefix={<Icon type="lock" style={{ fontSize: 13 }} />}
              type="password"
              placeholder=""
            />
          )}
        </FormItem>

        <FormItem>
          { 
            errorMsg && <div className="error_message">{errorMsg}</div> 
          }
          <Button
            type="primary"
            htmlType="submit"
            className="login-form-button"
            style={{ width: '100%' }}
            loading={loading}
          >
            {
              loading ? '' : ''
            }
          </Button>
          <div>
            <div></div>
          { /* eslint-disable-next-line*/}
            <a href="javascript:;" onClick={changeType.toLogin}></a>
          </div>
        </FormItem>

    
      </Form>
    );
  }
}

export default Form.create<IProps>()( LoginPanel );
